body {
  margin: 20px;
}

.search-form {
  width: 600px;
  justify-content: center;
  align-items: center;
  margin: 0 auto 20px;
  display: block;
}

.search-form input[type="text"] {
  width: 400px;
  border-radius: 4px;
  margin: 0 auto;
  padding: 8px 20px;
}

.search-more {
  color: #fff;
  background-color: #007bff;
  border: 3px;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  text-align: center;
  margin: 0 10px;
  font-size: 14px;
  display: flex;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  background-color: #007bff;
  border: 3px;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

.no-results {
  text-align: center;
  color: red;
  font-weight: bold;
}

.is-hidden {
  visibility: hidden;
  display: none;
}

